/* Reset ******************************/
* {margin:0; padding:0;}
html,body {height:100%;}
body {background:#fff; color:#333; font-weight:400; line-height:1;}
body,input,button,textarea,select {font-family:"Nanum Gothic", "나눔고딕", "Malgun Gothic", Dotum, 돋움, sans-serif; font-size:12px;}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display:block;}
h1, h2, h3, h4, h5, h6, strong, b {font-weight:700;}
ul,ol {list-style:none}
address,em,cite {font-style:normal;}
hr {display:none;}
img {border:0 none;}
button, input, select, textarea, img {vertical-align:middle;}
a, a:hover, a:focus, a:active {color:#333; text-decoration:none;}
label {vertical-align:middle; cursor:pointer;}
button {border:0 none; font-weight:700; cursor:pointer;}
*, *:before, *:after{-webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box;}
/**:focus {outline:none !important;}*/
button:focus {outline:none;}
sub, sup {font-size:75%; line-height:0; vertical-align:baseline;}
sup {top:-0.5em;}
sub {bottom:-0.25em;}
legend {position:absolute; visibility:hidden; overflow:hidden; width:0; height:0; margin:0; padding:0; font-size:0; line-height:0;}

/* Chart font */
svg * {font-family:"Nanum Gothic", "나눔고딕", "Malgun Gothic", Dotum, 돋움, sans-serif; font-weight:400;}
tspan {font-weight:700;}

/* Chrome, Safari용 스크롤 바 */
::-webkit-scrollbar {width:8px; height:8px; border:1px solid #fff;}
::-webkit-scrollbar-button:start:decrement,
::-webkit-scrollbar-button:end:increment {display:block; width:0; height:0;}
::-webkit-scrollbar-track {background:#efefef; border:1px solid #efefef;}
::-webkit-scrollbar-thumb {background:rgba(81,81,81,.9);}

/* Form ******************************/
form,fieldset,legend,label,input[type="image"], input[type="radio"], input[type="checkbox"] {border:0 none;}
input,select,textarea {color:#333;}
input[type="text"] ,input[type="password"],input[type="number"] {display:inline-block; min-width:10px; height:24px; line-height:16px; padding:2px 5px; border:1px solid #afafaf; background:#fff; border-radius:2px;}
input[type="number"] {text-align:right;}
input[type="text"]:focus ,input[type="password"]:focus {outline:0;}
input[type="text"].date {width:100px;}/* 달력 */
input[type="text"].date[disabled] + button.ui-datepicker-trigger {opacity:.4; pointer-events:none; cursor:no-drop;}/* 달력disabled시 아이콘 */
input[type="file"] {height:20px; border:1px solid #ccc;}
input[type="text"][disabled], input[type="password"][disabled] {border-color:#cfcfcf; color:#bfbfbf; cursor:no-drop;}
input[disabled] + label {opacity:0.5; cursor:no-drop;}
input[readonly] {border-color:#cfcfcf; background:#fff; color:#bfbfbf; cursor:default;}
textarea {width:100%; height:100%; border:none;}
textarea:focus {outline:0;}
textarea.text-box {width:100%; padding:2px 3px; border:1px solid #b2b2b2; border-radius:2px;}
textarea.text-box-gy {width:100%; padding:2px 3px; border:1px solid #cfcfcf; background:#efefef;}
textarea[disabled] {opacity:0.5; cursor:no-drop;} 
label {display:inline-block; height:24px; line-height:24px;}
label.checkbox-sm, label.radio-sm  {display:inline-block; height:20px; line-height:20px;}
input[readonly="readonly"].input-read {color:#333;}

/* file attach 2017-06-19 DCMS */
.file-attach {display:inline-block; vertical-align:middle;}
.file-attach:after {content:'';display:block;height:0;clear:both;visibility:hidden}
.file-attach input[type="text"] {float:left; width:334px;}
.file-attach .file-btn {overflow:hidden; float:left; position:relative; margin-left:4px; width:73px; height:24px;}
.file-attach .file-btn .btn-hide {position:absolute; top:0; left:0; width:73px; height:100%; opacity:0; filter:alpha(opacity=0); -ms-filter:"alpha(opacity=0)"; -khtml-opacity:0;-moz-opacity:0; cursor:pointer;}
.file-attach .file-btn .btn-cr,
.file-attach .file-btn .btn-gy {width:100%;}

/* Checkbox & Radio */
[type="checkbox"], 
[type="radio"] {width:14px; height:14px; margin:0 -14px 0 0; opacity:0; cursor:pointer;}
[type="checkbox"] + label, 
[type="radio"] + label {position:relative; padding:0 4px 0 18px;}
[type="checkbox"] + label:before {content:""; position:absolute; top:5px; left:0; width:14px; height:14px; border:1px solid #6c81df; background:#fff;}
[type="checkbox"]:checked + label:before {content:"\f00c"; -webkit-box-shadow:inset 0 1px 0 0 rgba(197,197,197,.6); box-shadow:inset 0 1px 0 0 rgba(197,197,197,.6); font:normal normal normal 12px/1 FontAwesome; color:#465fce; vertical-align:middle; -webkit-font-smoothing:antialiased; -moz-osx-font-smoothing:grayscale;}
[type="radio"]+label:before {content:""; position:absolute; top:5px; left:0; width:14px; height:14px; border:1px solid #6c81df; border-radius:50%; background:#fff;}
[type="radio"]:checked + label:before {-webkit-box-shadow:inset 0 1px 0 0 rgba(197,197,197,1); box-shadow:inset 0 1px 0 0 rgba(197,197,197,1);}
[type="radio"]:checked + label:after {content:""; position:absolute; top:8px; left:3px; width:8px; height:8px; border-radius:50%; background:#465fce;}
[type="checkbox"] + label.no-txt, 
[type="radio"] + label.no-txt {width:14px; margin:0; padding:0; font:0/0 x; color:rgba(0,0,0,0);}/* label안에 text가 없는 경우 */
.part[type="checkbox"]:checked + label:before {color:#c1c1c1;}/* check시에 회색으로 color표시(개발팀요청) */

[type="checkbox"] + label.checkbox-sm, 
[type="radio"] + label.radio-sm {position:relative; padding:0 4px 0 18px;}
[type="checkbox"] + label.checkbox-sm:before {content:""; position:absolute; top:4px; left:0; width:13px; height:13px; border:1px solid #6c81df; background:#fff;}
[type="checkbox"]:checked + label.checkbox-sm:before {content:"\f00c"; -webkit-box-shadow:inset 0 1px 0 0 rgba(197,197,197,.6); box-shadow:inset 0 1px 0 0 rgba(197,197,197,.6); font:normal normal normal 11px/1 FontAwesome; color:#465fce; vertical-align:middle; -webkit-font-smoothing:antialiased; -moz-osx-font-smoothing:grayscale;}
[type="radio"]+label.radio-sm:before {content:""; position:absolute; top:4px; left:0; width:13px; height:13px; border:1px solid #6c81df; border-radius:50%; background:#fff;}
[type="radio"]:checked + label.radio-sm:before {-webkit-box-shadow:inset 0 1px 0 0 rgba(197,197,197,1); box-shadow:inset 0 1px 0 0 rgba(197,197,197,1);}
[type="radio"]:checked + label.radio-sm:after {content:""; position:absolute; top:7px; left:3px; width:7px; height:7px; border-radius:50%; background:#465fce;}

/* SelectBox Basic */
.selectbox {display:inline-block; position:relative; min-width:50px; vertical-align:middle;}
.selectbox .current {overflow:hidden; display:block; height:24px; line-height:22px; padding:0 20px 0 5px;  border:1px solid #b2b2b2; border-radius:2px; background:#fff; text-align:left;overflow: hidden;text-overflow:ellipsis;white-space:nowrap;}
.selectbox .current:hover {color:#333; cursor:pointer;}
.selectbox .current:after {content:"\f078"; position:absolute; top:-moz-calc(50% - 8px); top:-webkit-calc(50% - 8px); top:calc(50% - 8px); right:6px; font:normal normal normal 14px/1 FontAwesome; color:#6b6b6b; -webkit-font-smoothing:antialiased; -moz-osx-font-smoothing:grayscale;}
.selectbox.on {z-index:1000;}
.selectbox.on a.current {border:1px solid #9b9b9b; color:#222;}
.selectbox.on a.current:after {top:-moz-calc(50% - 6px); top:-webkit-calc(50% - 6px); top:calc(50% - 6px); -moz-transform:rotate(-180deg); -webkit-transform:rotate(-180deg); transform:rotate(-180deg); opacity:1;}
.selectbox.on ul {display:block;}
.selectbox.dis {opacity:0.5;}
.selectbox.dis .current {color:#797979; cursor:no-drop;}
.selectbox.dis .current:hover {color:#797979;}
.selectbox.dis.on {z-index:-1;}
.selectbox.p100 {width:100% !important;}
.selectbox ul.selectbox-sm-ul {top:23px;}
/* 2016-08-24 DEL (아래 selectbox-sm-ul 개발분리용으로 사용하고 상속은 top만 잡아줌)
.selectbox ul {display:none; position:absolute; top:23px; left:0; right:0; max-height:161px; border:solid 1px #b2b2b2; border-radius:3px; background:#fff; overflow:auto; overflow-x:hidden; z-index:9999999999;}
.selectbox ul li {overflow:hidden; height:26px; line-height:18px; padding:4px 5px; border-top:dotted 1px #dadbd6; cursor:pointer;}
.selectbox ul li a {display:block; text-align:left;}
.selectbox ul li.on {font-weight:bold;}
.selectbox ul li:first-child {border-top:none;}
.selectbox ul li.over,.selectbox ul li.over a {background:#edf0f2; color:#000;}
.selectbox ul li label {display:block;}
.selectbox ul li label > input[type="checkbox"] {margin:0 5px 0 0;}
.selectbox.focus {}
.selectbox.shadow.focus a.current {border:1px solid #997ae1; -webkit-box-shadow:inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(153, 122, 225, .6); box-shadow:inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(153, 122, 225, .6);}
.selectbox.shadow.on a.current {border:1px solid #997ae1; -webkit-box-shadow:inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(153, 122, 225, .6); box-shadow:inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(153, 122, 225, .6);} */

