/* Button ***********/
/* Button 정렬 */
.btns-area {padding:10px; background:#fff; text-align:center;}/* 기본 중앙 */
.btns-area-right {padding:10px 0; text-align:right;}
.btns-area-left {padding:10px 0; text-align:left;}
.btns-area-bar {display:inline-block; margin:0 0 0 3px; padding:0 0 0 7px; background:url(../../images/common/bg_line_y.png) no-repeat 0 0; vertical-align:middle;}
.btns-area-control {position:absolute;} 

/* Button Common */
a[class^="btn-"], button[class^="btn-"] {display:inline-block; min-width:10px; text-align:center;}
a[class^="btn-"] + a[class^="btn-"] {margin:0 0 0 2px;}
button[class^="btn-"] + button[class^="btn-"] {margin:0 0 0 2px;}
a[class*="btn-"].ico, button[class*="btn-"].ico {font-size:0; line-height:0; width:30px; min-width:30px !important; padding:0;}
a[class^="btn-"][disabled], 
button[class^="btn-"][disabled], 
a[class*="btn-"][disabled]:hover, 
button[class^="btn-"][disabled]:hover {cursor:no-drop; opacity:0.6;}
a[class^="btn-"]:before, button[class^="btn-"]:before, 
.ico-lg:before, .ico-md:before, .ico-sm:before, .ui-datepicker-trigger:before, .btn-round:before, .ico-in:before {display:inline-block; font:normal normal normal 14px/1 FontAwesome; vertical-align:-1px; -webkit-font-smoothing:antialiased; -moz-osx-font-smoothing:grayscale;}
.ico-lg:before {vertical-align:-2px;}
a[class^="btn-"].ico:before, button[class^="btn-"].ico:before {margin:0;}/* icon만 있는 Basic Button */
.icon-group button + button {margin:0 0 0 2px;}
/* Button 100% block */
a[class^="btn-"].btn-block, button[class^="btn-"].btn-block {display:block; width:100%; text-align:left;}
button[class^="btn-"].btn-block + button[class^="btn-"].btn-block {margin:5px 0 0 0;}

/* Button Basic Color */
.btn-cr {height:24px; min-width:63px !important; line-height:22px; padding:0 6px; border:1px solid #5b6cb7; border-radius:2px; background:#6477cd linear-gradient(to bottom, #6b7fdc, #5c6ebf); -webkit-box-shadow:inset 0 1px 0 0 rgba(137,153,228,1); box-shadow:inset 0 1px 0 0 rgba(137,153,228,1); color:#fff;}
.btn-cr:hover {background:#6477cd linear-gradient(to bottom, #6a7eda, #5b6dbd, #6f80cc);}
.btn-cr:hover,
.btn-cr:hover:before,
.btn-cr:focus,
.btn-cr:active {color:#fff;}
.btn-cr[disabled] {background:#6477cd linear-gradient(to bottom, #6b7fdc, #5c6ebf); color:#d4d9f2;}
.btn-cr[disabled]:hover:before {color:#d4d9f2;}
.btn-cr:before {margin-right:8px;}

/* Button Basic Gray */
.btn-gy {height:24px; min-width:63px !important; line-height:22px; padding:0 6px; border:1px solid #afafaf; border-radius:2px; background:#ededed linear-gradient(to bottom, #fefefe, #e4e4e4); color:#333;}
.btn-gy:hover {background:#ededed linear-gradient(to bottom, #fdfdfd, #e2e2e2, #fbfbfb);}
.btn-gy:hover,
.btn-gy:hover:before,
.btn-gy:focus,
.btn-gy:active {color:#465fce;}
.btn-gy[disabled] {background:#ededed linear-gradient(to bottom, #fefefe, #e4e4e4); color:#333;}
.btn-gy[disabled]:hover:before {color:#333;}
.btn-gy:before {margin-right:8px;}

/* Button Small 2017-06-19 DCMS */
button[class*="btn-sm"] {height:20px; line-height:15px; font-size:11px;}
button[class*="btn-sm"]:before {font-size:12px;}

/* Button Large */
button[class*="btn-lg"] {height:34px; line-height:31px; font-size:14px;}
button[class*="btn-lg"]:before {font-size:18px;}

/* Button xLarge */
button[class*="btn-xl"] {height:64px; line-height:31px; font-size:14px;}
