/* Tooltip Box ******************************/
/* Tooltip Box White */
.tooltip {position:absolute; border:1px solid #d3d3d3; border-radius:2px; -moz-box-shadow:0 0 3px 0 #d4d4d4; -webkit-box-shadow:0 0 3px 0 #d4d4d4; box-shadow:0 0 3px 0 #d4d4d4; background:#fff; z-index:99999;}
.tooltip .tooltip-header {position:relative; height:25px; background:#f6f6f6;}
.tooltip .tooltip-header .title {height:25px; line-height:25px; font-size:12px;}
.tooltip .tooltip-header .btn-close {top:5px;}
.tooltip .tooltip-content {padding:10px 7px;}
.tooltip .in-box {padding:10px 12px;}
.tooltip .in-box2 {padding:0 7px 7px;}
.tooltip .in-box3 {padding:7px;}
.tooltip.border-none {border:none; -webkit-box-shadow:none; box-shadow:none;}/* tooltip의 용도가 그냥 띄우는 position일때 */
.tooltip.border-type {border-color:#bbb; border-radius:0;}/* tooltip 타입 분리 */
.tooltip.opacity {background-color:rgba(255,255,255,.8);}
.tooltip.opacity.tail-top:after,
.tooltip.opacity.tail-right:after,
.tooltip.opacity.tail-bottom:after,
.tooltip.opacity.tail-left:after {opacity:.8;}

/* Tooltip Box White + Tail */
.tooltip.tail-top:after, 
.tooltip.tail-top:before {content:" "; position:absolute; bottom:100%; left:50%; width:0; height:0; border:solid transparent; pointer-events:none;}
.tooltip.tail-right:after, 
.tooltip.tail-right:before {content:" "; position:absolute; top:50%; left:100%; width:0; height:0; border:solid transparent; pointer-events:none;}
.tooltip.tail-bottom:after, 
.tooltip.tail-bottom:before {content:" "; position:absolute; top:100%; left:50%; width:0; height:0; border:solid transparent; pointer-events:none;}
.tooltip.tail-left:after, 
.tooltip.tail-left:before {content:" "; position:absolute; top:50%; right:100%; width:0; height:0; border:solid transparent; pointer-events:none;}
.tooltip.tail-top:after {border-color:rgba(255, 255, 255, 0); border-bottom-color:#fff; border-width:5px; margin-left:-5px;}
.tooltip.tail-top:before {border-color:rgba(101, 107, 119, 0); border-bottom-color:#d3d3d3; border-width:7px; margin-left:-7px;}
.tooltip.tail-right:after {border-color:rgba(255, 255, 255, 0); border-left-color:#fff; border-width:5px; margin-top:-5px;}
.tooltip.tail-right:before {border-color:rgba(101, 107, 119, 0); border-left-color:#d3d3d3; border-width:7px; margin-top:-7px;}
.tooltip.tail-bottom:after {border-color:rgba(255, 255, 255, 0); border-top-color:#fff; border-width:5px; margin-left:-5px;}
.tooltip.tail-bottom:before {border-color:rgba(101, 107, 119, 0); border-top-color:#d3d3d3; border-width:7px; margin-left:-7px;}
.tooltip.tail-left:after {border-color:rgba(255, 255, 255, 0); border-right-color:#fff; border-width:5px; margin-top:-5px;}
.tooltip.tail-left:before {border-color:rgba(101, 107, 119, 0); border-right-color:#d3d3d3; border-width:7px; margin-top:-7px;}

/* 2016-01-21 ADD: 개발추가건 left값 변동 */
.tooltip.tail-top1:after,
.tooltip.tail-top1:before {content:" "; position:absolute; bottom:100%; left:72%; width:0; height:0; border:solid transparent; pointer-events:none;}
