/* Alert Tooltip Color */
.alert-name {display:inline-block; width:100px; padding:2px 5px; font-weight:normal; vertical-align:middle;}
.alert-icon {display:inline-block; width:32px; height:18px; line-height:18px; text-align:center; background:#e15554; border-radius:8px; color:#fff; vertical-align:middle; cursor:default;}
.critical {background:#ed1941;}
.major {background:#f3800f;}
.minor {background:#ffc000;}

/* Popup Basic */
.pop-wrap {position:absolute; top:0; left:0; border:1px solid #bbb; border-radius:1px; background:#fff; box-shadow:0 2px 5px rgba(0,0,0,0.2); z-index:30;}
.pop-wrap.resize {width:100%; height:100%; border:none; border-radius:0; box-shadow:none;}/* window popup 100%일때 */
.pop-header {position:relative; height:25px; border-top:solid 1px #fff; background:linear-gradient(to bottom,#e4e4e4,#d5d5d5);}
.pop-header .title {height:23px; line-height:23px; padding:0 0 0 8px;}
.pop-content {position:relative; padding:6px;}
.pop-content:after {content:""; display:block; clear:both;}

/* List Basic 공통사용 */
.basic-list {}
.basic-list > li {line-height:18px;}
.basic-list > li > label {overflow:hidden; max-width:100%; white-space:nowrap; text-overflow:ellipsis;}
.basic-list > li > a {display:block; padding:3px 5px; color:#6b6b6b; text-align:left;}
.basic-list > li > a:hover,
.basic-list > li > a:focus {background:#e9ecff; color:#262626;}

/* List Basic2(line) 공통사용 */
.basic-list2 {}
.basic-list2 > li {line-height:18px; border-top:1px solid #dfdfdf;}
.basic-list2 > li:first-child {border-top:none;}
.basic-list2 > li > a {display:block; padding:3px 10px; color:#6b6b6b; text-align:left;}
.basic-list2 > li > a:hover,
.basic-list2 > li > a:focus {background:#e9ecff; color:#262626;}

/* List Basic3 공통사용 */
.basic-list3 {padding:15px 10px;}
.basic-list3 > li {margin-top:5px;} 
.basic-list3 > li:first-child {margin-top:0;} 

/* List Drag Bookmark (line + draggable) */
.drag-mark-list {}
.drag-mark-list li {overflow:hidden; position:relative; height:27px; line-height:20px; margin-bottom:-1px; padding:2px 0 2px 10px; border-top:1px solid #dfdfdf; border-bottom:1px solid #dfdfdf; color:#6b6b6b; text-align:left; cursor:s-resize;}
.drag-mark-list li div {display:inline-block; vertical-align:middle;}
.drag-mark-list li .name {float:left; overflow:hidden; max-width:650px; width:calc(100% - 406px); white-space:nowrap; text-overflow:ellipsis;}
.drag-mark-list li .query-group {float:left; position:relative; width:390px; padding:0 0 0 10px;}
.drag-mark-list li .query {float:left; overflow:hidden; width:330px; white-space:nowrap; text-overflow:ellipsis;}
.drag-mark-list li .ico-group {float:right;}
.drag-mark-list li:before {content:"\f07d"; float:left; display:inline-block; padding:3px 10px 0 0; font:normal normal normal 14px/1 FontAwesome; vertical-align:middle; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale;}
.drag-mark-list li:hover {background:#e9ecff;}

/* side-menu */
ul[class^="side-menu"] {padding:5px 0;}
