ul[class^="side-menu"] li:hover {background:#d8ddff;}
.side-menu li.on {background:#6a7eda; color:#fff; font-weight:700;}
.side-menu li.on:hover {background:#6a7eda;}
.side-menu li.on a {color:#fff;}
.side-menu2 li {position:relative;}
.side-menu2 li.on {background:#cad1ff; font-weight:700;}
