/* Table */
table {border-collapse:collapse; border-spacing:0; width:100%; table-layout:fixed; word-wrap:break-word; word-break:break-all;}
caption {visibility:hidden; overflow:hidden; width:0; height:0; font-size:0; line-height:0;}
.th-right table tbody th {text-align:right !important;}/* th 전체가 right일때 (기본은 left) */
.td-center table tbody td {text-align:center !important;}/* td 전체가 center일때 (기본은 left) */

