.grid-tbl-body {width:100%; overflow-x:hidden; overflow-y:scroll; border:1px solid #ccc; border-top:none; border-left:none;}
.grid-tbl-body table tbody tr td {height:29px; line-height:18px; padding:2px 6px; border-left:1px solid #cfcfcf; border-top:1px solid #cfcfcf; text-align:center;}
